        <p class="tab" v-if="isEditable || !infoData.ifPost" :class="{active: infoData.ifPost === 0}" @click="switchPost(0)">暂不发帖<img v-if="this.isEditable && infoData.ifPost === 0" src="~@/assets/img/post-active.png"></p>
