<template>
  <div class="index-container">
    <keep-alive>
      <router-view v-if="
          userData.singleStatus !== 6 && userData.singleStatus !== 7 ||
          $route.name === 'Account'
        "/>
      <NotSingle v-else />
    </keep-alive>
    <div class="footer">
      <router-link to="/" class="tab home-btn" :class="{'active': $route.name === 'Home'}">
        <span class="tab-icon"></span>
        <span class="tab-name">缘分</span>
      </router-link>
      <router-link to="/messages.html" class="tab messages-btn" active-class="active">
        <span class="tab-icon"></span>
        <span class="tab-name">消息</span>
        <span class="message-num" v-if="unreadCount">{{unreadCount}}</span>
      </router-link>
      <router-link to="/account.html" class="tab account-btn" active-class="active">
        <span class="tab-icon"></span>
        <span class="tab-name">我的</span>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import NotSingle from '@/components/common/notSingle.vue'
export default {
  name: 'Index',
  components: {
    NotSingle
  },
  data () {
    return {
      timer: null
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapState('message', {
      unreadCount: 'unreadCount'
    })
  },
  methods: {
    ...mapActions('message', {
      fetchUnreadCount: 'fetchUnreadCount'
    }),
    handleUnreadCount () {
      this.fetchUnreadCount().then(
        () => {
          this.timer = setTimeout(this.handleUnreadCount, 20000)
        }
      )
    }
  },
  mounted () {
    this.handleUnreadCount()
  },
  destroyed () {
    clearTimeout(this.timer)
  }
}
</script>

