<template>
  <div class="need-login">
    <div class="not-login">
      <div class="head img-box">
        <img src="~@/assets/img/default-head.png" alt="" class="img" />
      </div>
      <span class="status">未登录</span>
    </div>
    <a href="javascript:;" @click="wxAuthor" class="webchat-login common-bg"><img class="webchat-logo"
        src="~@/assets/img/webchat-logo.png" />微信登录</a>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import Storage from '@/utils/Storage'
import wxApi from '@/utils/wxAuth'

export default {
  name: 'Login',
  computed: {
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    // 根据code 换用户信息
    async getToken (authorCode) {
      try {
        // authorCode = '0716evS72mB6GL086KQ72cZMS726evS1'
        this.$root.openLoading()
        let { code, data, msg } = await this.$http('/api/login/wx/accesstoken', { code: authorCode || '' }, 'get')
        this.$root.closeLoading()
        if (!(code === '0000' && data)) {
          return this.$toast(msg)
        }
        Storage.save('token', data.accessToken || '')
        Storage.save('userInfo', JSON.stringify(data))
        Storage.save('userId', data.currentMemberId)
        this.updateUserData(data)
        if (data.status === 0) {
          this.$router.push('setUserData.html')
        }
      } catch (error) {
        this.$toast(error)
      }
    },
    wxAuthor () {
      if (this.userId) {
        return
      }
      Storage.remove('token')
      this.$root.openLoading()
      wxApi.init({
        appid: /test|localhost/.test(location.host) ? 'wx92f49109825dac77' : 'wx2bdc6bb81e3f570e', // 正式测试号
        redirectUri: location.href
      }).author()
      this.$root.closeLoading()
    }
  },
  created () {
    // 微信回调返回了 code,直接去请求登录接口
    if (wxApi.getUrlParams().code !== undefined && !Storage.load('token')) {
      this.getToken(wxApi.getUrlParams().code)
    }
  }
}
</script>

