  padding: 0 40px;
  .title {
    font-size: 28px;
    line-height: 46px;
  }
  .sub-title {
    margin-bottom: 34px;
    color: #9B9BA1;
    font-size: 28px;
    line-height: 46px;
  }
  .confirm-item {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding: 18px 24px 18px 40px;
    border: 1px solid #979797;
    border-radius: 8px;
    color: #4A4A4A;
    .icon {
      margin-left: 16px;
      width: 10px;
    }
    .val-con {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
    }
