    line-height: 44px;
  }
  .tips {
    font-size: 24px;
    line-height: 36px;
  }
  .code {
    margin: 10px auto 30px;
    width: 260px;
    height: 260px;
  }
  .effect {
    margin-bottom: 4px;
    color: #9b9ba1;
    font-size: 24px;
    line-height: 36px;
  }
}
