    padding: 20px 40px;
    border-bottom: 1px solid #e4e4e4;
  }
  .title {
    font-size: 42px;
    line-height: 60px;
    font-weight: bold;
  }
  .num {
    color: #9b9b9b;
  }
  .list {
    padding-left: 40px;
  }
  .item-child {
    margin-bottom: 30px;
  }
  .item {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 5px 40px 5px 0;
    border-bottom: 1px solid #e5e5e5;

    .name {
      color: #38383c;
      font-size: 32px;
      line-height: 102px;
    }
    .price-date {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
    }
    .date {
      margin-top: 6px;
      color: #9b9b9b;
      font-size: 20px;
      line-height: 28px;
    }
  }
}
</style>
