  .list {
    padding-left: 40px;
    border-bottom: 1px solid #e5e5e5;
  }
  .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 50px 40px 0;
    .name,
    .value {
      color: #9B9BA1;
      font-size: 32px;
    }
    & + .item {
      border-top: 1px solid #e5e5e5;
    }
  }
  .enter {
    width: 14px;
  }
  .logout-btn {
    position: fixed;
    bottom: 60px;
    left: 50%;
    padding: 25px;
    width: 590px;
    color: $mainColor;
    font-size: 36px;
    line-height: 50px;
    border-radius: 50px;
    text-align: center;
    border: 1px solid $mainColor;
    transform: translateX(-50%);
  }
}
</style>
